

.board > div {
	display: grid;
	grid-template-columns: 8vmin repeat(9, 8vmin) 8vmin;
	grid-template-rows: 8vmin repeat(9, 8vmin) 8vmin;
	grid-gap: 0.02vmin;
	margin: 1vmin;
	width: fit-content;
	height: fit-content;
	background: antiquewhite;
	border: 0.2vmin solid black;
}

.center {
	grid-column: 2 / 11;
	grid-row: 2 / 11;
	background: antiquewhite;
	display: grid;
	grid-template-columns: repeat(7, 8vmin);
	grid-template-rows: repeat(7, 8vmin);
	justify-items: center;
	align-items: center;
	border: 0.2vmin solid black;
}

.row {
	display: grid;
	grid-gap: 0.02vmin;
}

.horizontal-row {
	grid-template-columns: repeat(9, 8vmin);
	grid-template-rows: 8vmin;
}

.vertical-row {
	grid-template-columns: 8vmin;
	grid-template-rows: repeat(9, 8vmin);
}

.bottom-row {
	grid-column: 2 / 11;
	grid-row: 11;
}

.left-row {
	grid-column: 1;
	grid-row: 2 / 11;
}

.top-row {
	grid-column: 2 / 11;
	grid-row: 1;
}

.right-row {
	grid-column: 11;
	grid-row: 2 / 11;
}

