body {
  font: 2vmin "Century Gothic", sans-serif;
  margin: 0.2vmin;
  background-color: wheat;
}

ol, ul {
  padding-left: 30px;
}

p {  
  display: block;
  margin-block-start: 0.7vmin;
  margin-block-end: 0.7vmin;
  margin-inline-start: 0px;
  margin-inline-end: 0px;
}

.game {
  transition: 1s ease-out;
  
}

.skewed {
  transform: rotateZ(90deg) scaleY(0);
}

.swal2-popup {
  width: 40vmin;
  font-size: 2vmin;
}

.game-info {
  margin-left: 0.2vmin;
  display: inline-flex;
  align-items: center;
  text-align: center;
}

.game-info > p {
  margin: 0.2vmin;
  padding: 0.2vmin;
  border-left: 0.1vmin solid black;
  border-bottom: 0.1vmin solid black;
}


.button-container {
  margin-left: 5vmin;
  font-size: 5vmin;
  width: 90vmin;
}

.status {
  margin-bottom: 10px;
}

.kbd-navigation .square:focus {
  background: #ddd;
}

.game {
  display: flex;
  width: 100vmin;
  flex-direction: column;
  margin-top: 2.5vmin;
  justify-content: center;
  /* align-items: center; */
  min-height: 15vh;
}

.scores-container {
  margin-right: 10vmin;
  color: rgb(208,33,41);
}

.create-button {
  margin-right: 2vmin;
  width: fit-content;
  height: 5vmin;
  background-color: aqua;
  border-radius: 0.5vmin;
}

.join-button {
  width: 10vmin;
  height: 5vmin;
  background-color: rgb(255, 200, 47);
  border-radius: 0.5vmin;
}

.input-class{
  margin-top: -0.1vmin;
}

.title-class {
  font-size: 1.5rvmin; 
}

.popup-class{
  height: 165px; 
}

.join-button-class {
  margin: .3125vmin; 
  padding: .5vmin 1.8vmin; 
  margin-bottom: 1vmin; 
}

.button-class {
  margin: .3125vmin; 
  padding: .5vmin 1.8vmin; 
}

.control {
  position: relative;
  top: -80vmin;
  left: 12vmin;
  width: 65vmin;
  height: 70vmin;
  overflow: scroll;
}

.pawn {
  position: absolute;
  transition: 1s ease-in-out;
  background: #dbdbdb;
  box-shadow: 0.2vmin 0.2vmin #888888;
  border: 0.5vmin solid black;
  left: 1vmin;
  top: 3vmin;
  width: 2vmin;
  height: 2vmin;
  border-radius: 50%;
  text-align: center;
  font-weight: bold;
}

#pawn1 {
  left: 1vmin;
  top: 3vmin;
}

#pawn2 {
  left: 6vmin;
  top: 5vmin;
}

#pawn3 {
  left: 1vmin;
  top: 7vmin;
}

#pawn4 {
  left: 6vmin;
  top: 9vmin;
}


.square {
  text-align: center;
}

.detail {
  width: 9vmin;
  visibility: hidden;
  position: absolute;
  display: block;
  position: absolute;
  border: 2px solid grey;
  background: lightsteelblue;
  z-index: 5;
  line-height: 1.5vmin;
}

.showDetail > .detail {
  visibility: visible;
}

.playersInfo {
  height: fit-content;
  width: 25%;
}

.playersInfo:hover > .detail{
  visibility: visible;
}

.playersInfo:focus > .detail{
  visibility: visible;
}

.control > button {
  width: 21vmin;
  height: 3.0vmin;
  background-color: lightgreen;
  border-radius: 0.5vmin;
  margin: 0.2vmin;
  font-size: 1vmin;
  display: inline-flex;
  align-items: center;
  justify-content: center;
}

@media only screen and (max-width: 600px) {
	.control > button {
    width: 30vmin;
    height: 4.0vmin;
  }
}

button:hover {
  cursor: pointer;
  border: 2px black solid;
}

button:disabled {
  cursor: auto;
  background-color: white;
}

button:disabled:hover {
  cursor: not-allowed;
  border: none;
  background-color: white;
}

.bottomPanel {
  display: flex;
  flex-direction: column;
}

.playersInfoWrapper {
  display: flex;
  text-align: center;
  border: 0.4vmin solid white;
  padding: 0.5vmin;
  height: fit-content;
}

.chatArea {
  display: flex;
  flex-direction: column;
  background-color: lightgoldenrodyellow;
}

.chatArea > .chatInputArea {
  background-color: antiquewhite;
}

#chatInput {
  width: 30vmin;
}

.chatInputArea > button {
  width: 12vmin;
  background-color: lightseagreen;
  border-radius: 0.5vmin;
  font-size: 1.5vmin;
}

.chatInputArea > input {
  font-size: 1vmin;
}

.chatArea > textarea {
  font-size: 2vmin;
  height: 16vmin;
}

@media only screen and (max-width: 600px) {
	.chatArea > textarea {
    height: 8vmin;
  }
}

#tradePanel {
  position: absolute;
  top: 10vmin;
}

.tradeWrapper {
  width: 60vmin;
  height: 11vmin;
  border: 0.4vmin solid yellowgreen;
  font-size: 1vmin;
  overflow: scroll;
  padding: 0.5vmin;
  background-color: white;
}

@media only screen and (max-width: 600px) {
	.tradeWrapper {
    height: 22vmin;
  }
}

.tradeWrapper > button {
  width: 12vmin;
  height: fit-content;;
  background-color: salmon;
  border-radius: 0.5vmin;
  font-size: 1vmin;
}


.rules {
  position: absolute;
  top: 95vmin;
  left: 5vmin;
  width: 85vmin;
}

.dice1 {
  background-image:  url("media/red1.png");
}
.dice2 {
  background-image:  url("media/red2.png");
}
.dice3 {
  background-image:  url("media/red3.png");
}
.dice4 {
  background-image:  url("media/red4.png");
}
.dice5 {
  background-image:  url("media/red5.png");
}
.dice6 {
  background-image:  url("media/red6.png");
}

.animation {
  position: absolute;
  width: 10vmin;
  height: 10vmin;
  top: 50vmin;
  transition: 1s;
  background-repeat: no-repeat;
  background-size: cover;
}
